<template>
  <div style="background: #b99; height: 620px;"> Main侧边栏导航菜单撑大右边，看右边高度是否增高<br/>
  </div>
</template>
<script>
export default {
  methods: {
    toggle: function () {
      if (this.$store.getters.sideWith === 180) {
        this.$store.commit('setSideWith', 40)
      } else {
        this.$store.commit('setSideWith', 180)
      }
    }
  }
}
</script>



